/*font*/

.circe-regular {
  font-family: Circe;
  font-weight: 400;
}

.circe-light {
  font-family: Circe;
  font-weight: 300;
}

.circe-bold {
  font-family: Circe;
  font-weight: 700;
}

.circe-extrabold {
  font-family: Circe;
  font-weight: 800;
}

.cabin {
  font-family: 'Cabin', sans-serif;
  font-weight: 400;
}

html {
  font-size: 10px;
}

.wrapper {
  max-width: 1643px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.wrapper .content {
  border: 2px solid #f5f5f5;
  border-top: none;
}

.wrapper .content .box {
  border-right: 1px solid #ececec;
  height: 234px;
  position: absolute;
  width: 756px;
}

/*color*/

/*main*/

.taitel {
  width: calc(52.5% - 10px);
  position: relative;
  top: 148px;
  z-index: 1;
  text-transform: uppercase;
  margin-left: -14px;
  margin-bottom: -90px;
}

.taitel span {
  font-family: Circe;
  font-weight: 800;
  color: #ffffff;
  font-size: 18.6rem;
  line-height: 160.236px;
  text-align: left;
  text-shadow: 0px 20px 90px rgba(36, 33, 31, 0.1);
  margin: 0;
}

/*nav-tab*/

nav {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  margin: 0;
}

.navRight {
  height: 910px;
  width: 70%;
  min-width: 800px;
  margin-bottom: -80px;
  position: relative;
  justify-content: center;
  display: flex;
  align-items: center;
  order: 1;
  background-color: #ffffff;
  box-shadow: 0px 30px 120px 0px rgba(0, 0, 0, 0.2);
}

.navRight__up {
  position: absolute;
  bottom: 50px;
  cursor: pointer;
  right: 30px;
}

.navRight__down {
  position: absolute;
  top: 50px;
  cursor: pointer;
  right: 30px;
}

.navRight__top {
  display: flex;
  flex-wrap: wrap;
  width: auto;
}

.navRight__text {
  font-size: 48px;
  color: #282828;
  line-height: 44px;
  padding-top: 100px;
  font-family: Circe;
  font-weight: 800;
}

.navRight__text .top__text {
  display: block;
  font-size: 23px;
  font-family: Circe;
  font-weight: 400;
  color: rgba(35, 35, 35, 0.5);
}

.navRight__img {
  margin-right: 40px;
  margin-left: 20px;
}

.navRight__img_text {
  padding-top: 45px;
  max-width: 547px;
  font-size: 18.67px;
  font-family: 'Cabin', sans-serif;
  font-weight: 400;
  color: rgba(53, 53, 53, 0.6);
  line-height: 32px;
}

.navLeft {
  width: calc(23.333333333333332% - 10px);
  align-self: center;
  position: relative;
  z-index: 1;
  margin-top: 80px;
}

.navLeft__top {
  margin: 90px 0 25px 0;
}

.navLeft__down {
  margin: 30px 0 10px 0;
}

.navLeft .uk-tab {
  margin: 0;
  font-family: Circe;
  font-weight: 400;
  margin-left: 3px;
}

.navLeft .uk-tab li {
  border-top: 1px solid #ccc;
}

.navLeft .uk-tab li a {
  transform: rotate(-90deg);
  font-size: 21.33px;
}

.navLeft .uk-tab > .uk-active > a {
  color: #f9c97b;
  border-color: #f9c97b;
  font-weight: 700;
}

.navLeft .uk-tab > .uk-active > .line {
  font-size: 0;
  height: 2px;
  width: 326px;
  padding: 0;
  background-color: #f9c97b;
  top: 108px;
  left: 126px;
}

.uk-tab-right > * > a {
  border-top: 6px solid transparent;
  border-left: none;
}

.uk-tab-right {
  background-color: #ffffff;
  box-shadow: 0px 20px 90px 0px rgba(36, 33, 31, 0.15);
  display: flex;
}

.uk-switcher {
  margin-top: 120px;
}

.uk-tab > * > a {
  padding: 20px 25px;
  text-transform: none;
}

.line {
  position: absolute;
  width: 216px;
  border: none !important;
  transform: rotate(0deg) !important;
  transform: translateY(-74px) !important;
}

.icon-arrowUP:before {
  cursor: pointer;
  padding: 15px;
}

.icon-arrow-down:before {
  cursor: pointer;
  padding: 15px;
}

.navLeft .uk-tab li a {
  font-size: 19px;
}

.flexnav {
  display: flex;
}

/*stiky menu*/

.menuNav {
  order: 1;
  position: fixed;
  right: 1.4%;
  z-index: 1;
  box-shadow: 0px 30px 180px 0px rgba(0, 0, 0, 0.24);
}

.menuNav__style {
  list-style: none;
  padding: 0;
  width: 64px;
  height: 346px;
}

.menuNav__style a {
  text-decoration: none;
  color: #d3d3d3;
  padding: 21px;
}

.menuNav__style a:hover {
  color: #222;
}

.menuNav__style a:active {
  color: #f9c97b;
}

.menuNav__style_up {
  text-align: center;
  padding: 20px 0 20px 0;
  border-bottom: 1px solid #ccc;
}

.menuNav__style_social {
  padding: 23px 0 23px 0;
  text-align: center;
  cursor: pointer;
}

.menuNav__style_social:hover {
  background: url(../img/icon/line.svg) no-repeat center right;
  background-size: 20px;
}

.menuNav__style .icon-BEMenu {
  padding: 16px;
}

.sticky {
  margin-top: -13vh;
  transition: all 0.9s ease;
  box-shadow: none;
}

.sticky a:hover {
  color: #f9c97b;
}

/*srvice-button*/

section {
  height: 500px;
  background-color: #edecec;
}

section .wrapper {
  height: 430px;
  display: flex;
  align-items: center;
}

.models {
  border: 1px solid #222;
  padding: 40px 80px 40px 80px;
  margin: 65px auto 0 22.6%;
  width: 70%;
  box-sizing: border-box;
}

.models__taitel {
  display: flex;
  font-size: 23px;
}

.models__taitel span {
  color: rgba(35, 35, 35, 0.5);
  font-family: Circe;
  font-weight: 400;
}

.models__text {
  color: #282828;
  font-size: 5.8rem;
  display: flex;
  align-items: center;
  font-family: Circe;
  font-weight: 700;
}

.models__text_taitel {
  width: calc(71.66666666666667% - 10px);
}

.models__text button {
  margin-bottom: 10px;
  background-color: #ffffff;
  box-shadow: 0px 10px 43px 0px rgba(0, 0, 0, 0.13);
  font-size: 13.33px;
  color: #202020;
  font-family: Circe;
  font-weight: 700;
  width: 280px;
  height: 50px;
  text-transform: uppercase;
  line-height: 14px;
  outline: none;
  border: none;
  cursor: pointer;
}

.models__text button:hover {
  box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.2);
  color: #f9c97b;
}

.models__text button:active {
  background-color: #f5f2f2;
  color: #202020;
  box-shadow: none;
}

.models__text button .btn {
  border-bottom: 4px solid #f9c97b;
  color: #202020;
  width: 220px;
  text-decoration: none;
  position: relative;
  bottom: -16px;
  margin: auto;
  font-family: Circe;
  font-weight: 700;
}

.icon-arrowUP,
.icon-arrow-down {
  margin-left: 20px;
}

/*footer*/

.footerBG {
  background: url(../img/logotype.png) #222 no-repeat center center;
  background-size: cover;
}

footer {
  height: 1200px;
  display: flex;
  width: 100%;
  align-items: flex-end;
}

footer .flex-content {
  width: 100%;
}

.ftTop {
  transform: translateY(-315px);
}

.ftTop__text {
  font-family: Circe;
  font-weight: 700;
  text-align: center;
  font-size: 6.2rem;
}

.ftTop__text span {
  display: block;
  color: #ffffff;
}

.ftTop__button {
  position: relative;
  top: 71px;
  text-align: center;
}

.uk-button {
  background: url(../img/border.png) no-repeat center;
  background-size: contain;
  color: #f9c97b;
  font-size: 23.18px;
  text-transform: none;
  width: 240px;
  height: 63px;
  line-height: 57px;
  border: none;
}

.uk-button:active {
  background-color: rgba(198, 138, 67, 0.4);
  color: #f9c97b;
}

.uk-button:hover {
  color: #f9c97b;
}

.uk-modal-close-full {
  background: none;
  left: auto;
  top: -36%;
  color: #eac28f !important;
}

.uk-modal-close-full:hover {
  color: #fff !important;
}

.uk-modal-close-full:active {
  color: #c68a43!important;
}

.uk-background-cover {
  background: url(../img/logotype.png) #222 no-repeat bottom center;
  background-size: cover;
  height: 100%;
  display: flex;
  text-align: center;
}

/*form*/

.contact {
  align-self: center;
  position: relative;
  max-width: 910px;
  margin: 0 auto;
  width: 100%;
  font-weight: 300;
  font-family: Circe;
  font-weight: 700;
}

.contact input::placeholder {
  color: rgba(220, 174, 127, 0.6);
}

.contact__flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 22px;
}

.contact__submit {
  margin-top: 49px;
}

.contact span {
  font-size: 42.67px;
  color: #ffffff;
}

.contact input {
  margin-right: 20px;
  width: 248px;
  height: 42px;
  background-color: rgba(186, 135, 82, 0.03);
  border: 1px solid #eac28f;
  outline: none;
  color: rgba(255, 251, 245, 0.7);
  font-family: Circe;
  font-weight: 300;
  margin-top: 10px;
  margin-bottom: 14px;
  box-sizing: border-box;
  padding-left: 22px;
}

.contact__message textarea {
  background-color: rgba(186, 135, 82, 0.03);
  width: 250px;
  height: 168px;
  border: 1px solid #eac28f;
  outline: none;
  color: rgba(255, 251, 245, 0.7);
  font-family: Circe;
  font-weight: 300;
  resize: none;
}

.contact__form .error {
  border-color: #ff0000;
  background-color: rgba(186, 135, 82, 0.03);
}

.contact__form .error::placeholder {
  color: #ff0000;
  margin-left: 0;
}

.contact label.error {
  color: #fff;
  background-color: #ff0000;
  position: absolute;
  margin-left: 1%;
  z-index: 1;
  margin-top: -95px;
  display: block;
  padding: 10px 25px 10px 25px;
  font-size: 13.33px;
  font-family: 'Cabin', sans-serif;
  font-weight: 400;
}

.contact label.error::after,
.contact label.error::before {
  content: '';
  position: absolute;
  background: #ff0000;
  left: 20px;
  bottom: -9px;
  width: 20px;
  height: 20px;
  z-index: -1;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.contact label.error::before {
  z-index: 1;
  box-shadow: none;
}

.contact .valid {
  border-color: #eac28f;
  background: rgba(186, 135, 82, 0.03) url(../img/icon_check.svg) no-repeat 210px 13px;
}

/*down*/

.ftBottom {
  display: flex;
  flex-wrap: wrap;
}

.ftBottom__text {
  font-size: 16px;
  width: calc(33.333333333333336% - 10px);
  text-transform: uppercase;
}

.ftBottom__text span {
  font-family: Circe;
  font-weight: 700;
}

.ftBottom__text .arts {
  color: #f9c97b;
}

.ftBottom__service {
  width: calc(35.00000000000001% - 10px);
}

.ftBottom__service_social {
  text-decoration: none;
  list-style: none;
  display: flex;
  justify-content: center;
}

.ftBottom__service_social li {
  margin: 0 16px 0 16px;
}

.ftBottom__service_social li a {
  color: #666;
  text-decoration: none;
}

.ftBottom__service_social li a:hover {
  color: #f9c97b;
}

.ftBottom__email {
  font-size: 16px;
  width: calc(33.333333333333336% - 10px);
  text-align: right;
  text-transform: uppercase;
}

.ftBottom__email a {
  text-decoration: none;
  color: #f9c97b;
  font-family: Circe;
  font-weight: 700;
}

.bottom {
  text-align: right;
  padding: 47px 0 47px 0;
  font-size: 12px;
  text-transform: uppercase;
  font-family: Circe;
  font-weight: 700;
}

.bottom span {
  margin-right: 10px;
}

/*media*/

@media only screen and (max-width: 1463px) {
  html {
