  }
}

@media only screen and (max-width: 1389px) {
  .navRight__img {
    order: 1;
  }

  .navRight__top {
    justify-content: center;
  }

  .uk-switcher {
    margin-top: 0;
  }

  .models {
    padding: 40px 10px 40px 10px;
  }
}

